.app {
  text-align: center;
}

.app-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.app-header {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-column-gap: 0.75rem;
  padding: 0.75rem;
  background-color: #145be9;
  align-items: center;
  font-size: 1.5rem;
  color: white;
}

.header-title {
  grid-column-start: 2;
  grid-column-end: 5;
}

.header-config {
  grid-column-start: 5;
}

.center {
  margin: auto;
}

.right {
  float: right;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

.config-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.config-row-item {
  padding-left: 1rem;
  min-width: 2rem;
}

.score {
  margin: 0.5rem;
  font-weight: bold;
  font-size: 2rem;
  padding-top: 0.5rem;
}

.question-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 0.5rem;
}

.question {
  font-size: 1rem;
  padding-left: 0.5rem;
  width: 100%;
}

.answer {
  padding-left: 0.5rem;
  padding-top: 0.5rem;
  width: 100%;
}

.answer input[type="number"] {
  width: 5rem;
  font-size: 1rem;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

.answer input[type="number"]::-webkit-inner-spin-button,
.answer input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.answer input[type="submit"] {
  font-size: 1rem;
  margin-left: 0.5rem;
}

.result {
  font-size: 1.5rem;
  font-weight: bold;
  color: red;
  min-height: 2.5rem;
  display: block;
  padding-top: 0.5rem;
}

.result.correct {
  color: green;
}

.label-text {
  padding-right: 0.5rem;
}

.medium-integer {
  width: 5rem;
}

.symbol {
  font-size: 1.5rem;
}

.hidden {
  display: none !important;
}

.switch {
  position: relative;
  display: inline-block;
  width: 2.5rem;
  height: 1.5rem;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 1rem;
  width: 1rem;
  left: 0.25rem;
  bottom: 0.25rem;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(1rem);
  -ms-transform: translateX(1rem);
  transform: translateX(1rem);
}

/* Rounded sliders */
.slider.round {
  border-radius: 2rem;
}

.slider.round:before {
  border-radius: 50%;
}

.history {
  width: 90%;
  margin: auto;
  text-align: center;
  border: 1px solid grey;
  border-collapse: collapse;
}

.history td, th {
  padding: 0.1rem 1rem;
  border: 1px solid grey;
}

.history-answer {
  width: 15%;
}

.history-number {
  width: 5%;
}

.history-question {
  width: 50%;
}

.history-correct {
  width: 15%;
}
